.top-section {
    padding-top: 129px;
    background-color: #014e56;
    height: 596px;

    .main-container {
        background-color: #014e56;
    }

    .top-cotent {
        color: aliceblue;
        justify-content: space-between;
    }
    .top-moto {
        width: 635px;
        height: 200px;
        font-size: 100px;
        font-family: Livvic-Bold;

        line-height: 100px;
    }

    .top-desc {
        align-self: flex-end;
        width: 445px;
        p {
            font-family: Livvic-SemiBold;
            font-size: 18px;
        }
    }

    @include for-mobile-layout {
    }
}
.middle-section {
    padding-top: 140px;
    background-color: #012f34;
    height: 700px;

    .middle-cotent {
        height: 404px;
        color: aliceblue;
        justify-content: space-between;
    }
    .middle-moto {
        flex-direction: column;
        justify-content: space-between;
        width: 445px;
        height: 202px;
        font-size: 30px;

        div {
            height: 10px;
            width: 50px;
            background-color: #f67e7e;
        }
        h1 {
            font-size: 37px;
        }
    }

    .middle-desc {
        height: 346px;
        align-self: flex-end;
        width: 540px;
        flex-direction: column;
        justify-content: space-between;

        .desc-row {
            display: flex;
            height: 94px;
            justify-content: space-between;

            div {
                display: flex;
                flex-direction: column;
                width: 445px;

                p {
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                }
            }
        }

        img {
            width: 72px;
            height: 72px;
        }
    }

    @include for-mobile-layout {
    }
}
.buttom-section {
    padding-top: 140px;
    background-color: #004047;
    height: 745px;
    position: relative;

    .top-square {
        position: absolute;
        top: 0;
    }

    .wheel-img {
        position: absolute;
        bottom: 0;
        right: 0;
    }

    .buttom-cotent {
        height: 465px;
        color: aliceblue;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }

    .buttom-moto {
        display: flex;
        flex-direction: column;
        width: 932px;
        height: 96px;
        font-size: 30px;
        h1 {
            text-align: center;
            font-family: Livvic-Bold;
            font-size: 48px;
        }
    }

    .buttom-desc {
        height: 313px;
        justify-content: space-between;

        .desc-col {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 350px;
            height: 313px;
            justify-content: space-between;
            position: relative;

            .quotes {
                position: absolute;
                z-index: 0;
            }

            h1 {
                text-align: center;
                font-size: 13px;
                &:first-of-type {
                    color: #79c8c7;

                    font-family: Livvic-Bold;
                    font-size: 18px;
                }
            }

            p {
                font-family: Livvic-SemiBold;
                font-size: 15px;
                text-align: center;
                padding-top: 36px;
                z-index: 1;
            }

            img {
                width: 62px;
                height: 62px;
                border-radius: 50%;
                border: #c4fffe 2px solid;
            }
        }
    }

    @include for-mobile-layout {
    }
}

.homepage-about {
    background-image: url(https://res.cloudinary.com/dstqymucm/image/upload/v1622467842/petMe/hero-img/boy-and-cat_xqeqwq.jpg);
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 500px;

    @include for-mobile-layout {
        height: 335px;
    }

    .blur {
        background-color: #0000002e !important;
        display: flex;
        align-items: center;
        justify-content: center;
        backdrop-filter: blur(4px);
        width: 100%;
        height: 100%;

        .about-content {
            color: $clr6;
            margin: 0 25% 0;

            .welcome-logo {
                width: 150px;
                align-self: center;

                @include for-mobile-layout {
                    width: 100px;
                }
            }

            @include for-mobile-layout {
                margin: 0;
                padding: 2% 10%;
                font-size: small;
            }

            > *:not(:last-child) {
                margin-block-end: 10px;
            }
        }
    }
}
